import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { 
  Check, 
  Star, 
  Zap, 
  Shield, 
  Users, 
  TrendingUp,
  Award,
  Headphones,
  FileText,
  BarChart3,
  Settings,
  Globe
} from 'lucide-react';
import { Link } from 'react-router-dom';

const SubscriptionPage = () => {
  const [selectedPlan, setSelectedPlan] = useState('professional');

  const plans = [
    {
      id: 'basic',
      name: '',
      price: '',
      period: '',
      description: '',
      features: [
        '',
        '50',
        '',
        '',
        ''
      ],
      limitations: [
        '5',
        '',
        ''
      ],
      color: 'border-gray-300',
      bgColor: 'bg-gray-50',
      buttonColor: 'bg-gray-600 hover:bg-gray-700'
    },
    {
      id: 'professional',
      name: '',
      price: 'SGD 29',
      period: '/',
      description: '',
      features: [
        'CRM',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      limitations: [
        '20',
        ''
      ],
      color: 'border-orange-500',
      bgColor: 'bg-orange-50',
      buttonColor: 'bg-orange-600 hover:bg-orange-700',
      popular: true
    },
    {
      id: 'enterprise',
      name: '',
      price: 'SGD 99',
      period: '/',
      description: '',
      features: [
        '10',
        '',
        'API',
        '',
        '',
        '',
        '',
        '',
        '24/7',
        ''
      ],
      limitations: [
        '',
        ''
      ],
      color: 'border-purple-500',
      bgColor: 'bg-purple-50',
      buttonColor: 'bg-purple-600 hover:bg-purple-700'
    }
  ];

  const benefits = [
    {
      icon: TrendingUp,
      title: '',
      description: '40%'
    },
    {
      icon: Award,
      title: '',
      description: ''
    },
    {
      icon: Users,
      title: '',
      description: ''
    },
    {
      icon: Shield,
      title: '',
      description: ''
    }
  ];

  const trainingCourses = [
    {
      title: '',
      duration: '2',
      level: '',
      certificate: true
    },
    {
      title: '',
      duration: '3',
      level: '',
      certificate: true
    },
    {
      title: '',
      duration: '4',
      level: '',
      certificate: true
    },
    {
      title: '',
      duration: '5',
      level: '',
      certificate: true
    }
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Hero Section */}
      <section className="bg-gradient-to-r from-orange-600 to-purple-700 text-white py-20">
        <div className="container mx-auto px-4 text-center">
          <motion.h1
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-4xl md:text-6xl font-bold mb-6"
          >
            
          </motion.h1>
          <motion.p
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.2 }}
            className="text-xl text-orange-100 max-w-2xl mx-auto"
          >
            
          </motion.p>
        </div>
      </section>

      {/* Subscription Plans */}
      <section className="py-16">
        <div className="container mx-auto px-4">
          <div className="grid md:grid-cols-3 gap-8">
            {plans.map((plan, index) => (
              <motion.div
                key={plan.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
                className={`relative ${plan.bgColor} border-2 ${plan.color} rounded-lg p-8 ${
                  plan.popular ? 'ring-4 ring-orange-200' : ''
                }`}
              >
                {plan.popular && (
                  <div className="absolute -top-4 left-1/2 transform -translate-x-1/2">
                    <span className="bg-orange-600 text-white px-4 py-2 rounded-full text-sm font-medium">
                      
                    </span>
                  </div>
                )}

                <div className="text-center mb-8">
                  <h3 className="text-2xl font-bold text-gray-900 mb-2">{plan.name}</h3>
                  <p className="text-gray-600 mb-4">{plan.description}</p>
                  <div className="mb-6">
                    <span className="text-4xl font-bold text-gray-900">{plan.price}</span>
                    <span className="text-gray-600">{plan.period}</span>
                  </div>
                </div>

                <div className="space-y-4 mb-8">
                  <h4 className="font-semibold text-gray-900">:</h4>
                  {plan.features.map((feature, idx) => (
                    <div key={idx} className="flex items-center">
                      <Check className="w-5 h-5 text-green-500 mr-3 flex-shrink-0" />
                      <span className="text-gray-700">{feature}</span>
                    </div>
                  ))}
                </div>

                {plan.limitations && (
                  <div className="space-y-2 mb-8">
                    <h4 className="font-semibold text-gray-900">:</h4>
                    {plan.limitations.map((limitation, idx) => (
                      <div key={idx} className="flex items-center">
                        <span className="w-2 h-2 bg-gray-400 rounded-full mr-3"></span>
                        <span className="text-gray-600 text-sm">{limitation}</span>
                      </div>
                    ))}
                  </div>
                )}

                <button
                  className={`w-full ${plan.buttonColor} text-white py-3 px-6 rounded-lg font-semibold transition-colors`}
                >
                  {plan.id === 'basic' ? '' : ''}
                </button>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Benefits Section */}
      <section className="py-16 bg-white">
        <div className="container mx-auto px-4">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-center mb-12"
          >
            <h2 className="text-3xl font-bold text-gray-900 mb-4"></h2>
            <p className="text-gray-600 max-w-2xl mx-auto">
              
            </p>
          </motion.div>

          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8">
            {benefits.map((benefit, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
                className="text-center"
              >
                <div className="bg-orange-100 w-16 h-16 rounded-full flex items-center justify-center mx-auto mb-4">
                  <benefit.icon className="w-8 h-8 text-orange-600" />
                </div>
                <h3 className="text-xl font-semibold text-gray-900 mb-2">{benefit.title}</h3>
                <p className="text-gray-600">{benefit.description}</p>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Training Section */}
      <section className="py-16 bg-gray-50">
        <div className="container mx-auto px-4">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            className="text-center mb-12"
          >
            <h2 className="text-3xl font-bold text-gray-900 mb-4"></h2>
            <p className="text-gray-600 max-w-2xl mx-auto">
              
            </p>
          </motion.div>

          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-6">
            {trainingCourses.map((course, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
                className="bg-white rounded-lg shadow-md p-6"
              >
                <div className="flex items-center justify-between mb-4">
                  <h3 className="font-semibold text-gray-900">{course.title}</h3>
                  {course.certificate && (
                    <Award className="w-5 h-5 text-yellow-500" />
                  )}
                </div>
                <div className="space-y-2 text-sm text-gray-600">
                  <p>: {course.duration}</p>
                  <p>: {course.level}</p>
                </div>
                <button className="w-full mt-4 bg-orange-600 text-white py-2 px-4 rounded hover:bg-orange-700 transition-colors">
                  
                </button>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-16 bg-orange-600">
        <div className="container mx-auto px-4 text-center">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
          >
            <h2 className="text-3xl font-bold text-white mb-4">
              
            </h2>
            <p className="text-orange-100 mb-8 max-w-2xl mx-auto">
              
            </p>
            <div className="space-x-4">
              <Link
                to="/register"
                className="bg-white text-orange-600 px-8 py-3 rounded-lg font-semibold hover:bg-gray-100 transition-colors"
              >
                
              </Link>
              <Link
                to="/contact"
                className="border-2 border-white text-white px-8 py-3 rounded-lg font-semibold hover:bg-white hover:text-orange-600 transition-colors"
              >
                
              </Link>
            </div>
          </motion.div>
        </div>
      </section>
    </div>
  );
};

export default SubscriptionPage; 